import React, { useState } from "react";
import {
  AppBar,
  Tabs,
  Tab,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
} from "@material-ui/core";
import "./ProfileLinkMaker.css";
import ImageIcon from "@material-ui/icons/Image";
import QueryBuilderIcon from "@material-ui/icons/QueryBuilder";
import FolderIcon from "@material-ui/icons/Folder";

function ProfileLinkMaker() {
  const [toggleState, setToggleState] = useState(0);

  const handleTabs = (e, val) => {
    setToggleState(val);
  };

  return (
    <div className="profileLinkMaker">
      <AppBar position="static">
        <Tabs className="tabs" value={toggleState} onChange={handleTabs}>
          <Tab label="Link" />
          <Tab label="EMBED Music" />
        </Tabs>
      </AppBar>
      <TabPanel value={toggleState} index={0}></TabPanel>
      <TabPanel value={toggleState} index={1}></TabPanel>
    </div>
  );
}

function TabPanel({ value, index }) {
  return (
    <div className="tabPanel">
      {value === index && (index === 0 ? <LinkAdder /> : <MusicEmbedAdder />)}
    </div>
  );
}

function MusicEmbedAdder() {
  return (
    <div className="musicEmbedAdder">
      <div className="input_wrapper">
        <FormControl>         
          <Select>
           
            <MenuItem value='APPLE_EMBED' >Apple Music</MenuItem>
            <MenuItem value='APPLE_EMBED'>Beatstars</MenuItem>
            <MenuItem value='APPLE_EMBED'>Mixcloud</MenuItem>
            <MenuItem value='APPLE_EMBED'>Soundcloud</MenuItem>
            <MenuItem value='APPLE_EMBED'>Spotify</MenuItem>
            <MenuItem value='APPLE_EMBED'>Tital</MenuItem>
          </Select>
        </FormControl>
        <input type="link" placeholder="https://example.com/song" />
      </div>
      <div className="linkAdder__footer">
        <div className="buttons-wrapper">
         
          <button>
            <QueryBuilderIcon />
          </button>
          <button>
            <FolderIcon />
          </button>
        </div>
        <button>Add Embed</button>
      </div>
    </div>
  );
}

function LinkAdder() {
  return (
    <div className="linkAdder">
      <div className="input__wrapper">
        <input type="text" placeholder="Title Example" />
        <input type="link" placeholder="https://example.com" />
      </div>
      <div className="linkAdder__footer">
        <div className="buttons-wrapper">
          <button>
            <ImageIcon />
          </button>
          <button>
            <QueryBuilderIcon />
          </button>
          <button>
            <FolderIcon />
          </button>
        </div>
        <button>Add Link</button>
      </div>
    </div>
  );
}
export default ProfileLinkMaker;
