import React from "react";
import "./ProfileHeader.css";
import LinkIcon from "@material-ui/icons/Link";
import NotificationsIcon from "@material-ui/icons/Notifications";
import MenuIcon from "@material-ui/icons/Menu";
import HeaderOption from "./HeaderOption";
import FormatPaintIcon from '@material-ui/icons/FormatPaint';
import PieChartIcon from '@material-ui/icons/PieChart';
import InstagramIcon from '@material-ui/icons/Instagram';
import PersonIcon from '@material-ui/icons/Person';

function ProfileHeader() {
  return (
    <div className="profileHeader">
      <div className="header_top">
        <div className="profileHeader__left">
          <div className="profile__name">
            <LinkIcon />

            <h4>Link.Bio</h4>
          </div>
        </div>
        <div className="profileHeader__right">
          <NotificationsIcon />
          <MenuIcon />
        </div>
      </div>
      <div className="header__bottom">
          <HeaderOption title='LINKS' Icon={LinkIcon} to='/manage'/>
          <HeaderOption title='STYLE' Icon={FormatPaintIcon} to='/manage/style'/>
          <HeaderOption title='STATS' Icon={PieChartIcon} to='/manage' />
          <HeaderOption title='SYNC'  Icon={InstagramIcon} to='/manage'/>
          <HeaderOption title='PROFILE' Icon={PersonIcon} to='/manage/profile'/>
      </div>
    </div>
  );
}

export default ProfileHeader;
